footer {
  background-color: #6AD38B;
  padding: 20px;
  margin-top: 2em;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
  color: #1F2D37;
  height: 100%;
}

.logo-container {
  align-items: center;
  margin-left: 50px;
  margin-bottom: -10px;
  margin-top: 80px;
}


.footer-nav,
.footer-nav2 {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 10px;
  margin-top: 50px;
  max-width: 200px;
  text-align: left;    
}

.footer-nav2 {
  margin-top: 55px;
}

.footer-nav a{
  margin: 20px 0;
  text-align: left;
  display: block;
  color: var(--secondary-gunmetal, #1F2D37);
}

.footer-nav2 a {
  margin: 10px 0;
  text-align: left;
  display: block;
  color: var(--secondary-gunmetal, #1F2D37);
}

.footer-nav h4,
.footer-nav2 h4 {
  margin: 0 10px;
}

.footer-column {
  flex: 1;
  display: row;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  max-width: 300px;
  margin-left: 20px;
  text-align: left;
}

.footer-column h2 {
  margin-top: 20px;
  margin-bottom: 100px;
}


.footer-nav {
  color: #1F2D36;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  
}

.footer-nav2 {
  display: flex;
  color: #1F2D36;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}


.footer-nav a {
  display: block; /* Update: Display as block element */
  margin-bottom: 5px; /* Add margin-bottom to separate the links */
  
}

.column:last-child {
  margin-right: 0;
}

.address {
  color: var(--secondary-gunmetal, #1F2D37);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.8;
  margin-top: 60px;
  text-align: left;
  margin-bottom: 50px;
  
}

@media screen and (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: auto;
  }
  
  .footer-column {
    margin-left: 0;
    margin-bottom: 20px;
    align-items: center;
    text-align: center;
  }
  
  .logo-container {
    margin-left: 0;
    margin-top: 20px;
    align-items: center;
    text-align: center;
  }
  
  .footer-nav,
  .footer-nav2 {
    max-width: 100%;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
    align-items: center;
  }
  
  .address {
    margin-top: 20px;
    margin-bottom: 20px;
    align-items: center;
    text-align: center;
  }
}